	data() {
		return {
			
		}
	},
	onLoad() {

	},
	methods: {
		go_next(data){
			console.log(data);
			if(!wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../mine/login'
				})
				return
			}
			wx.navigateTo({
				url: data
			})
		},
		go_mine_apply(){
			wx.navigateTo({
				url: '../mine/apply'
			})
		},
	}
}
